::-webkit-scrollbar{
    display:none;
}

.container {
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.loader{
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 30px;
    justify-content: center;
    display: flex;
    
}

.loader__img {
    position: fixed;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: space-between;
    width: 60px;
    animation: .5s rotateCircle infinite ease-in-out;

}

.point {
    width: 10px;
    height: 10px;
    background-color: black;
    border-radius: 5px;
  }

  @keyframes rotateCircle {
    from {
      transform: none;
    }
  
    to {
      transform: rotate(180deg);
    }
  }

body {
    background-color: #F0F4F8;
}

ul{
    list-style: none;
    position: absolute;
    left: 170px;
}
.movies {
    display: flex;
    margin: 30px;
    width: auto;
    flex-wrap: wrap;
    justify-content: space-around;
}



.movies__movie {
    position: relative;
    width: 456px;
    height: 250px;
    margin-bottom: 60px;
    display: flex;;
    background-color: #FFFFFF;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0 ,0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
}

.movie__year {
    position: absolute;
    right: 10px;
    top: -15px;
}
img{
    position: absolute;
    top: -14px;
    left: 20px;
    width: 120px;
    height: 180px;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0 ,0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

ul.genres{
    display: flex;
    opacity: 40%;
    left: 130px;
    bottom: 140px;
    font-size: 13px;
    flex-wrap: wrap;
}

.movie__title {
    position: absolute;
    left: 170px;
    top: 5px;
}

.movie__summary {
    position: absolute;
    left: 170px;
    top: 110px;
    height: 90px;
    overflow: auto;
    width: 250px;
    opacity: 40%;
    font-size: 13px;
}